@font-face {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ubuntu-Bold.woff2") format("woff2"),
    url("../fonts/ubuntu-Bold.woff") format("woff");
}

@font-face {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ubuntu-Regular.woff2") format("woff2"),
    url("../fonts/ubuntu-Regular.woff") format("woff");
}
